    .icon-kuozhan-zhankai-xian {
      cursor: pointer;
    }
  }
  .transferBelling {
    background: var(--color-warning);
  }
  .transferDialing {
    background: var(--color-warning)
  }
  .belling {
    background: var(--color-warning);
  }
  .hold {
    background-color: var(--color-success);
  }
  .dialing {
    background: var(--color-warning);
  }
  .ringing {
    background: var(--color-warning);
  }
  .dialTalking {
    background: var(--color-success);
  }
  .oprate {
    display: flex;
    .oprate-btn {
      width: 40px;
      height: 40px;
      border-radius: 8px;
