    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    top: calc(100% + 8px);
    left: 0;
    padding: 16px;
    box-sizing: border-box;
    .title {
      display: flex;
      flex-direction: row;
      margin-bottom: 10px;
      .text {
        flex: 1
        font-weight: 500;
        font-size: 14px;
      }
      .close {
        .iconfont {
          font-size: 20px;
          cursor pointer;
        }
      }
    }
    .info {
      font-weight: 400;
      font-size: 12px;
      color: rgba(0, 0, 0, 0.6);
    }
  }
}
</style>
